<template>
    <v-card class="card-container">
      <img
        id="profile-img"
        src="//ssl.gstatic.com/accounts/ui/avatar_2x.png"
        class="profile-img-card"
      />
        <v-text-field
            v-model="user.username"
            :rules="userNameRules"
            label="User name"
            required
        ></v-text-field>
        <v-text-field
            v-model="user.password"
            :rules="passwordRules"
            label="Password"
            type="password"
            required
        ></v-text-field>
        <div class="mt-4">
          <v-btn outlined color="indigo" class="btn-block" @click="login">
            <span v-show="loading" class="spinner-border spinner-border-sm"></span>
            <span>Login</span>
          </v-btn>
        </div>
        <div>
          <div v-if="message" class="alert alert-danger" role="alert">{{message}}</div>
        </div>
    </v-card>
</template>

<script>
import User from '../models/user';

export default {
  name: 'Login',
  data() {
    return {
      user: new User('', ''),
      loading: false,
      message: '',
      userNameRules: [(v) => !!v || "Имя пользователя не указано",
        (v) => (v && v.length <= 50) || "Длинна не должна превышать 50 символа",
        (v) =>
            (v && v.length >= 4) || "Длинна дожна быть не менее 4 символов",
      ],
      passwordRules: [(v) => !!v || "Пароль не указан",
        (v) => (v && v.length <= 50) || "Длинна не должна превышать 50 символов",
        (v) =>
            (v && v.length >= 6) || "Длинна дожна быть не менее 6 символов",
      ],
      snackbar: false,
      timeout: 3000,
    };
  },
  methods: {
    login() {
      this.loading = true;
        if (this.user.username && this.user.password) {
          this.$store.dispatch('auth/login', this.user).then(
            () => {
              this.$router.push('/timetibleList');
            },
            () => {
              this.loading = false;
              this.message = 'Возникла ошибка';
              this.snackbar = true;
            }
          );
        } else {
          this.loading = false;
          this.message = 'Не все поля заполнены';
          this.snackbar = true;
        }
    }
  }
};
</script>

<style scoped>
label {
  display: block;
  margin-top: 10px;
}

.card-container {
  max-width: 350px !important;
  padding-left: 40px;
  padding-right: 40px;
  padding-bottom: 20px;
  margin:auto;
  margin-top: 5%;
}

.profile-img-card {
  width: 96px;
  height: 96px;
  margin: 0 auto 10px;
  display: block;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  border-radius: 50%;
}
</style>